<template>
  <multiselect 
    v-model="localStage" 
    :options="stageOptions" 
    label="label" 
    track-by="value"
    placeholder="Select Stage"
    @update:modelValue="emitStageChange"
  />
</template>

<script>
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';

export default {
  components: {
    Multiselect,
  },
  props: {
    selectedStage: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      localStage: null, // Initialize localStage to null
      stageOptions: [], // Initialize stageOptions as an empty array
    };
  },
  mounted() {
    // Initialize stageOptions when the component mounts
    this.stageOptions = [
      { label: '', value: '' },
      { label: 'WhatsApp with some info', value: 'W' },
      { label: 'WhatsApp follow Up', value: 'WF' },
      { label: 'List Call', value: 'L' },
      { label: 'List Call follow Up', value: 'LF' },
      { label: 'Advertisement', value: 'A' },
      { label: 'Customer Follow up', value: 'C' },
      { label: 'Viewing', value: 'V' },
      { label: 'Payment & Signing', value: 'P' },
      { label: 'To receive money', value: 'M' },
      { label: 'Close', value: 'C' },
      { label: 'Terminate', value: 'X' },
    ];
    // Set the initial localStage based on selectedStage prop
    this.localStage = this.findStageOption(this.selectedStage);
  },
  watch: {
    selectedStage(newStage) {
      this.localStage = this.findStageOption(newStage); // Update localStage to match the new prop value
    },
  },
  methods: {
    findStageOption(value) {
      // Find and return the corresponding stage option object based on the value
      return this.stageOptions.find(option => option.value === value) || null;
    },
    emitStageChange() {
      // Emit the selected stage value back to the parent
      this.$emit('update-stage', this.localStage ? this.localStage.value : null);
    },
    getStageDescription(value) {
      // Use findStageOption to get the option, then return the label (description)
      const option = this.findStageOption(value);
      return option ? option.label : null;
    },
  },
};
</script>

